"use client";
import React, { useState } from "react";
import { useForm } from "react-hook-form";
import * as z from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";

const birthChartSchema = z.object({
  fullName: z.string().min(1, "Name is required"),
  dateOfBirth: z.string().min(1, "Date is required"),
  timeOfBirth: z.string().min(1, "Time is required"),
  location: z.string().min(1, "Location is required"),
  gender: z.enum(["male", "female", "other"], {
    required_error: "Please select gender",
  }),
});

type BirthChartFormValues = z.infer<typeof birthChartSchema>;

interface ChartResult {
  sunSign: string;
  moonSign: string;
  ascendant: string;
  analysis: string;
  strengths: string[];
  challenges: string[];
  recommendations: string[];
}

export default function BirthChartPage() {
  const [result, setResult] = useState<ChartResult | null>(null);
  const [isLoading, setIsLoading] = useState(false);

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<BirthChartFormValues>({
    resolver: zodResolver(birthChartSchema),
  });

  const generateChartResult = (data: BirthChartFormValues): ChartResult => {
    // Simulate birth chart calculation based on input
    const sunSigns = ["Aries", "Taurus", "Gemini", "Cancer", "Leo", "Virgo", "Libra", "Scorpio", "Sagittarius", "Capricorn", "Aquarius", "Pisces"];
    const moonSigns = ["Aries", "Taurus", "Gemini", "Cancer", "Leo", "Virgo", "Libra", "Scorpio", "Sagittarius", "Capricorn", "Aquarius", "Pisces"];
    const ascendants = ["Aries", "Taurus", "Gemini", "Cancer", "Leo", "Virgo", "Libra", "Scorpio", "Sagittarius", "Capricorn", "Aquarius", "Pisces"];
    
    const dateHash = new Date(data.dateOfBirth).getTime();
    const nameHash = data.fullName.length;
    
    const sunSign = sunSigns[dateHash % sunSigns.length];
    const moonSign = moonSigns[(dateHash + nameHash) % moonSigns.length];
    const ascendant = ascendants[(dateHash * nameHash) % ascendants.length];

    return {
      sunSign,
      moonSign,
      ascendant,
      analysis: `Your birth chart reveals a unique cosmic blueprint, ${data.fullName}. Born under the influence of ${sunSign} Sun, ${moonSign} Moon, and ${ascendant} Ascendant, you possess a distinctive combination of energies that shape your personality and life path.`,
      strengths: [
        "Natural leadership abilities",
        "Strong intuitive powers",
        "Creative and artistic talents",
        "Compassionate nature",
        "Analytical thinking"
      ],
      challenges: [
        "Tendency to overthink situations",
        "Need for emotional balance",
        "Learning to trust others",
        "Managing perfectionist tendencies"
      ],
      recommendations: [
        "Practice daily meditation for inner peace",
        "Engage in creative pursuits to channel energy",
        "Maintain regular exercise routine",
        "Cultivate meaningful relationships",
        "Study spiritual texts for guidance"
      ]
    };
  };

  async function onSubmit(data: BirthChartFormValues) {
    setIsLoading(true);
    try {
      // Simulate API call delay
      await new Promise((resolve) => setTimeout(resolve, 2000));
      const chartResult = generateChartResult(data);
      setResult(chartResult);
    } catch (error) {
      console.error(error);
    } finally {
      setIsLoading(false);
    }
  }

  return (
    <div className="py-10">
      {/* Header */}
      <section className="text-center mb-12">
        <h1 className="text-4xl md:text-5xl font-bold mb-4 text-transparent bg-clip-text bg-gradient-to-r from-yellow-400 to-orange-500" style={{ fontFamily: 'Cinzel, serif' }}>
          Birth Chart Calculator
        </h1>
        <div className="w-24 h-1 bg-gradient-to-r from-yellow-400 to-orange-500 mx-auto mb-6"></div>
        <p className="text-xl text-gray-300 mb-2">Unveil Your Cosmic Blueprint</p>
        <p className="text-gray-400 max-w-2xl mx-auto">
          Discover the celestial influences present at your birth. Your birth chart reveals the positions 
          of planets and their impact on your personality, relationships, and life path.
        </p>
      </section>

      <div className="max-w-4xl mx-auto">
        <div className="grid lg:grid-cols-2 gap-8">
          {/* Form Section */}
          <Card className="bg-black/40 backdrop-blur-md border-purple-500/30">
            <CardHeader>
              <CardTitle className="text-2xl text-yellow-400 text-center" style={{ fontFamily: 'Cinzel, serif' }}>
                Enter Your Birth Details
              </CardTitle>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
                <div>
                  <Label htmlFor="fullName" className="text-gray-300">Full Name</Label>
                  <Input
                    id="fullName"
                    type="text"
                    {...register("fullName")}
                    className="mt-1 bg-black/20 border-purple-500/30 text-white"
                    placeholder="Enter your full name"
                  />
                  {errors.fullName && (
                    <p className="text-red-400 text-sm mt-1">{errors.fullName.message}</p>
                  )}
                </div>

                <div>
                  <Label htmlFor="dateOfBirth" className="text-gray-300">Date of Birth</Label>
                  <Input
                    id="dateOfBirth"
                    type="date"
                    {...register("dateOfBirth")}
                    className="mt-1 bg-black/20 border-purple-500/30 text-white"
                  />
                  {errors.dateOfBirth && (
                    <p className="text-red-400 text-sm mt-1">{errors.dateOfBirth.message}</p>
                  )}
                </div>

                <div>
                  <Label htmlFor="timeOfBirth" className="text-gray-300">Time of Birth</Label>
                  <Input
                    id="timeOfBirth"
                    type="time"
                    {...register("timeOfBirth")}
                    className="mt-1 bg-black/20 border-purple-500/30 text-white"
                  />
                  {errors.timeOfBirth && (
                    <p className="text-red-400 text-sm mt-1">{errors.timeOfBirth.message}</p>
                  )}
                </div>

                <div>
                  <Label htmlFor="location" className="text-gray-300">Place of Birth</Label>
                  <Input
                    id="location"
                    type="text"
                    {...register("location")}
                    className="mt-1 bg-black/20 border-purple-500/30 text-white"
                    placeholder="City, Country"
                  />
                  {errors.location && (
                    <p className="text-red-400 text-sm mt-1">{errors.location.message}</p>
                  )}
                </div>

                <div>
                  <Label className="text-gray-300">Gender</Label>
                  <div className="mt-2 space-y-2">
                    <label className="flex items-center space-x-2">
                      <input
                        type="radio"
                        value="male"
                        {...register("gender")}
                        className="text-yellow-400"
                      />
                      <span className="text-gray-300">Male</span>
                    </label>
                    <label className="flex items-center space-x-2">
                      <input
                        type="radio"
                        value="female"
                        {...register("gender")}
                        className="text-yellow-400"
                      />
                      <span className="text-gray-300">Female</span>
                    </label>
                    <label className="flex items-center space-x-2">
                      <input
                        type="radio"
                        value="other"
                        {...register("gender")}
                        className="text-yellow-400"
                      />
                      <span className="text-gray-300">Other</span>
                    </label>
                  </div>
                  {errors.gender && (
                    <p className="text-red-400 text-sm mt-1">{errors.gender.message}</p>
                  )}
                </div>

                <Button
                  type="submit"
                  disabled={isLoading}
                  className="w-full bg-gradient-to-r from-yellow-500 to-orange-500 text-black font-semibold hover:from-yellow-400 hover:to-orange-400 transition-all duration-300"
                >
                  {isLoading ? "Calculating Your Chart..." : "Generate Birth Chart"}
                </Button>
              </form>
            </CardContent>
          </Card>

          {/* Result Section */}
          {result && (
            <Card className="bg-black/40 backdrop-blur-md border-purple-500/30">
              <CardHeader>
                <CardTitle className="text-2xl text-yellow-400 text-center" style={{ fontFamily: 'Cinzel, serif' }}>
                  Your Birth Chart Analysis
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-6">
                {/* Main Signs */}
                <div className="grid grid-cols-3 gap-4 text-center">
                  <div className="bg-purple-900/30 p-4 rounded-lg">
                    <h4 className="text-yellow-400 font-semibold">Sun Sign</h4>
                    <p className="text-white text-lg">{result.sunSign}</p>
                  </div>
                  <div className="bg-purple-900/30 p-4 rounded-lg">
                    <h4 className="text-yellow-400 font-semibold">Moon Sign</h4>
                    <p className="text-white text-lg">{result.moonSign}</p>
                  </div>
                  <div className="bg-purple-900/30 p-4 rounded-lg">
                    <h4 className="text-yellow-400 font-semibold">Ascendant</h4>
                    <p className="text-white text-lg">{result.ascendant}</p>
                  </div>
                </div>

                {/* Analysis */}
                <div>
                  <h4 className="text-yellow-400 font-semibold mb-2">Cosmic Analysis</h4>
                  <p className="text-gray-300 leading-relaxed">{result.analysis}</p>
                </div>

                {/* Strengths */}
                <div>
                  <h4 className="text-yellow-400 font-semibold mb-2">Your Strengths</h4>
                  <ul className="text-gray-300 space-y-1">
                    {result.strengths.map((strength, index) => (
                      <li key={index} className="flex items-center space-x-2">
                        <span className="text-green-400">âœ“</span>
                        <span>{strength}</span>
                      </li>
                    ))}
                  </ul>
                </div>

                {/* Challenges */}
                <div>
                  <h4 className="text-yellow-400 font-semibold mb-2">Areas for Growth</h4>
                  <ul className="text-gray-300 space-y-1">
                    {result.challenges.map((challenge, index) => (
                      <li key={index} className="flex items-center space-x-2">
                        <span className="text-orange-400">âš¡</span>
                        <span>{challenge}</span>
                      </li>
                    ))}
                  </ul>
                </div>

                {/* Recommendations */}
                <div>
                  <h4 className="text-yellow-400 font-semibold mb-2">Spiritual Recommendations</h4>
                  <ul className="text-gray-300 space-y-1">
                    {result.recommendations.map((rec, index) => (
                      <li key={index} className="flex items-center space-x-2">
                        <span className="text-purple-400">ðŸ”®</span>
                        <span>{rec}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              </CardContent>
            </Card>
          )}
        </div>

        {/* Contact CTA */}
        <section className="mt-16 text-center py-8 bg-gradient-to-r from-purple-900/30 to-blue-900/30 rounded-2xl backdrop-blur-md border border-purple-500/20">
          <h3 className="text-2xl font-semibold mb-4 text-yellow-400" style={{ fontFamily: 'Cinzel, serif' }}>
            Want Detailed Analysis?
          </h3>
          <p className="text-gray-300 mb-6 max-w-2xl mx-auto">
            For comprehensive birth chart reading and personalized guidance, 
            consult with Astrologer Vikas Lamba for deeper insights into your cosmic blueprint.
          </p>
          <a 
            href="/contact" 
            className="bg-gradient-to-r from-yellow-500 to-orange-500 text-black font-semibold px-8 py-3 rounded-full hover:from-yellow-400 hover:to-orange-400 transition-all duration-300 transform hover:scale-105"
          >
            Book Personal Consultation
          </a>
        </section>
      </div>
    </div>
  );
}
